
.kz-tree__wrapper {
  min-width: 250px;
  text-align: left;
}
.kz-tree__top {
  padding: 10px;
  text-align: right;
  border-bottom: 1px solid #ddd;
}
.el-tree-node {
  position: relative;
}
.el-tree-node__content:hover .kz-tree-bar {
  display: block;
}
.kz-tree-bar {
  display: none;
  position: absolute;
  top: 0;
  right: 10px;
  font-size: 12px;
}
.kz-tree-bar > i {
  margin: 0 5px;
  color: #999;
}
.kz-tree-bar > i:hover {
  color: #20a0ff;
}
.el-tree {
border: none;
}
.el-tree-node__label
{
  width: 120px;
  white-space: nowrap;
 text-overflow: ellipsis;
  overflow: hidden;
}



